*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body {
    font-family: system-ui;
    background-color: rgb(246, 246, 246);
    
}


.watchList {
    width: 80vw;
    display: grid ;
    gap: 2rem;
    margin:3rem auto;
    /* text-align: center; */
}


@media screen and (min-width: 600px) {
    .watchList {
        grid-template-columns: repeat(3,1fr);
    }    
}


.watch {
    background-color: #fff;
    border-radius: 5px;
    padding: 2rem;
    text-align: center;


}


.watch h3 {
    margin-top: 1rem;
}


.watch img {
    border: 1px solid rgb(201, 201, 201);
    max-width: 200px;
    max-height: 200px;
}


.author {
    color:rgb(119, 119, 119)
}